  border: red 2px solid;
  padding: 1.25em 2em;
  position: relative;
  height: 200px; }
  .catalog li:before, .catalog li:after,
  .catalog li span:before,
  .catalog li span:after {
    content: "";
    position: absolute;
    border-radius: 2px; }
  .catalog li span {
    color: black;
    z-index: 1000;
    border: 2px dotted forestgreen; }
  .catalog li:after {
    width: 40%;
    height: 20%;
    border: 2px solid #b18597;
    background-color: #f9c4d2;
