    bottom: 0;
    left: 5%;
    z-index: 2; }
  .catalog li span:after {
    width: 100%;
    height: 92%;
    background-color: #f9c4d2;
    border: 2px solid #b18597;
    background-color: #f9c4d2;
    bottom: 0;
    left: 0;
    z-index: 2;
    transition: transform 500ms ease 0ms;
    transform-origin: 0 100% 0;
    transform-style: preserve-3d;
    display: flex;
